<template>
  <div id="chatt">
    <div v-if="role == 1" @click="openList = !openList" class="toggle">
      <i class="fa fa-bars"></i>
    </div>
    <div :class="{ open: openList, user: role == 0 }" class="chat-cont clearfix">
      <div v-if="role == 1" class="people-list" id="people-list">
        <div class="search">
          <input @input="search" type="text" placeholder="search" />
          <i class="fa fa-search"></i>
        </div>
        <ul class="list">
          <div v-if="usersLoading" class="center">
            <loading />
          </div>
          <li
            @click="chooseChat(user)"
            v-for="user in filteredUsers"
            :key="user._id"
            class="clearfix d-flex"
            :class="{
              unread: user.msgs > 0,
              choosen: choosenUser && user._id == choosenUser._id,
            }"
          >
            <div class="img">
              <!---IMAGE--->
              <img src="../../assets/img/public/avatar.webp" alt="avatar" />
            </div>
            <div class="about">
              <div class="name">{{ user.fullname }}</div>
              <div v-if="onlineUsers.includes(user._id)" class="status">
                <i class="fa fa-circle online"></i>online
              </div>
              <div v-if="!onlineUsers.includes(user._id)" class="status">
                <i class="fa fa-circle offline"></i>offline
              </div>
            </div>

            <span v-if="user.msgs > 0" class="norm">{{ user.msgs }}</span>
          </li>
        </ul>
      </div>

      <div class="chat">
        <div style="width: 100%; height: 100%" v-if="!choosenUser" class="center">
          <h3>اختار طالب لبدأ المحادثة</h3>
        </div>
        <div v-if="choosenUser">
          <div class="chat-header d-flex clearfix">
            <div class="img2">
              <!---IMAGE--->
              <img src="../../assets/img/public/avatar.webp" alt="avatar" />
            </div>
            <div class="chat-about">
              <div v-if="role == 0">
                <div class="chat-with">ADMIN</div>
                <div class="status">
                  <i
                    class="fa fa-circle"
                    :class="{ online: adminOnline, offline: !adminOnline }"
                  ></i
                  >{{ adminOnline ? "online" : "offline" }}
                </div>
              </div>
              <div v-if="role == 1">
                <div class="chat-with">{{ choosenUser.fullname }}</div>
                <div class="chat-num-messages">
                  {{ choosenUser.phone }}
                </div>
              </div>

              <!----------------------->
            </div>
          </div>
          <!-- end chat-header -->

          <div id="messages" class="chat-history">
            <div v-if="!messagesLoading && messages.length == 0" class="center">
              <h3>لا يوجد رسائل</h3>
            </div>
            <div v-if="messagesLoading" class="center">
              <loading />
            </div>
            <ul id="messages">
              <li style="text-align: center" v-if="messages.length > 0">
                <a @click="loadMore">Load more</a>
              </li>
              <div v-for="(mess, i) in messages" :key="i">
                <li
                  v-if="mess.senderId == userId"
                  class="clearfix"
                  :class="{ seen: seen && i == messages.length - 1 }"
                >
                  <div class="message-data align-right">
                    <div>
                      <span class="message-data-name">{{ mess.senderName }}</span>
                      <i class="fa fa-circle me"></i>
                    </div>
                    <span class="message-data-time">{{
                      mess.date | moment("dddd, MMMM Do YYYY, h:mm a")
                    }}</span>
                  </div>
                  <i
                    @click="deleteMessage(mess.code, mess.message)"
                    class="fa fa-times"
                  ></i>
                  <div class="message my-message float-right">
                    <h5 v-if="mess.type == 'text'">{{ mess.message | truncate }}</h5>
                    <audio-player
                      v-if="mess.type == 'audio'"
                      :src="secureUrl(mess.message)"
                      direction="right"
                    />
                    <div v-if="mess.type == 'image'" class="img">
                      <img
                        :src="secureUrl(mess.message)"
                        @error="reloadImg(mess.code, mess.message)"
                        @click="reloadImg(mess.code, mess.mesage)"
                        class="img-fluid"
                      />
                    </div>
                  </div>
                </li>

                <li v-if="mess.senderId != userId">
                  <div class="message-data">
                    <span class="message-data-name"
                      ><i class="fa fa-circle online"></i> {{ mess.senderName }}</span
                    >
                    <div>
                      <span class="message-data-time">{{
                        mess.date | moment("dddd, MMMM Do YYYY, h:mm a")
                      }}</span>
                    </div>
                  </div>
                  <div class="message other-message">
                    <h5 v-if="mess.type == 'text'">{{ mess.message | truncate }}</h5>
                    <audio-player
                      v-if="mess.type == 'audio'"
                      :src="secureUrl(mess.message)"
                      direction="left"
                    />
                    <div v-if="mess.type == 'image'" class="img">
                      <img
                        :src="secureUrl(mess.message)"
                        @error="reloadImg(mess.code, mess.message)"
                        class="img-fluid"
                      />
                    </div>
                  </div>
                </li>
              </div>
              <!-- <div v-if="seen" class="seen">
                <h3 style="color: green" class="text-right">Seen</h3>
              </div> -->
            </ul>
          </div>
          <!-- end chat-history -->
          <div class="chat-message p-3 p-md-5 bg-light">
            <div v-if="imgBlobUrl" class="img-to-send row justify-content-center">
              <div class="col-md-3 col-9">
                <div class="img">
                  <i @click="removeToSendImg()" class="fa fa-times"></i>
                  <img :src="imgBlobUrl" class="img-fluid" />

                  <div class="uploading">
                    <loading v-if="uploading" />
                  </div>
                </div>
              </div>
            </div>
            <div v-if="recorder" class="recorder-cont">
              <audio-recorder :choosenUser="choosenUser" />
            </div>
            <!-- IMAGE -->
            <div class="options">
              <input
                style="display: none"
                type="file"
                @change="pickImage"
                ref="pickImg"
                accept="image/*"
              />
              <i @click="$refs.pickImg.click()" class="fa fa-file-image-o"></i>
              <i @click="recorder = !recorder" class="fa fa-microphone"></i>
            </div>
            <div class="bottom">
              <div class="input">
                <textarea
                  placeholder="Type your message"
                  v-model="message"
                  @keydown="
                    (e) => {
                      if (e.keyCode == '13') sendMessage();
                    }
                  "
                ></textarea>
                <emoji-picker @pick="pickEmoji" />
              </div>

              <button
                class="send-btn"
                :disabled="message == '' && !image"
                @click="sendMessage"
              >
                Send
              </button>
            </div>
          </div>
          <!-- end chat-message -->
        </div>
      </div>
      <!-- end chat -->
    </div>
    <!-- end container -->
  </div>
</template>

<script>
import Axios from "axios";
import { Message } from "./message.js";
import AudioRecorder from "./audio-recorder.vue";
import { socket } from "../../main.js";
import EmojiPicker from "./emoji-picker.vue";
import AudioPlayer from "./audio-player.vue";
export default {
  components: {
    EmojiPicker,
    AudioRecorder,
    AudioPlayer,
  },
  created() {
    this.$store.dispatch("changeMsgNumber", 0);
    if (this.role == 0) {
      this.choosenUser = { _id: this.userId };
      Axios.get(`/getMessages/${this.choosenUser._id}/${0}/${this.limit}`).then((res) => {
        this.messagesLoading = false;
        if (res.data.err) return;
        const msgs = res.data.messages.sort(
          (a, b) => new Date(a.date).getTime() - new Date(b.date).getTime()
        );
        this.messages = msgs;
        this.socket.emit("lastSeen", this.userId);
        this.isSeen("user");
        setTimeout(() => this.scrollDown(), 500);
      });
      setInterval(() => {
        this.socket.emit("getAdminOnline");
      }, 2000); //////////////////////
      this.socket.on("adminOnline", (online) => {
        this.adminOnline = online;
      });

      return;
    }
    ///////////////////////////////////////////////////////
    this.usersLoading = true;
    Axios.post("/getUsers/0/100").then((res) => {
      this.usersLoading = false;
      this.users = res.data.users;
      this.getUnreadMsgs();
    });
    //////////////////////////////////////
    setInterval(() => {
      this.socket.emit("getOnline");
    }, 2000);
    //////////////////////
    this.socket.on("onlineUsers", (onlineUsers) => {
      this.onlineUsers = onlineUsers;
    });
  },
  mounted() {
    this.socket.on("receiveMessage", (mess) => {
      console.log(this.role);
      if (this.choosenUser) {
        if (this.role == 0) this.socket.emit("lastSeen", this.userId);
        else if (this.role == 1) this.socket.emit("lastSeenAdmin", this.choosenUser?._id);
        ///////////////////////////////////////////
        if (mess.room == this.choosenUser?._id) {
          this.addMessage(mess);
        }
      }
      ////////////////////////////////////////////
      if (this.role == 1) {
        if (mess.senderId == this.userId) return;
        if (mess.room == this.choosenUser?._id) return;
        const index = this.users.findIndex((u) => u._id == mess.room);
        this.users[index]["msgs"] = this.users[index].msgs + 1;
        this.users = [...this.users];
      }
    });
    this.socket.on("msgDeleted", (code) => {
      const index = this.messages.findIndex((mess) => mess.code == code);
      this.messages.splice(index, 1);
    });
    this.socket.on("seen", (userId) => {
      if (userId != this.choosenUser?._id) {
        this.seen = true;
      }
    });
  },
  data() {
    return {
      openList: false,
      message: "",
      users: [],
      filteredUsers: [],
      usersLoading: false,
      messagesLoading: false,
      messages: [],
      limit: 50,
      choosenUser: null,
      socket: socket(),
      onlineUsers: [],
      adminOnline: false,
      image: null,
      imgBlobUrl: null,
      uploading: false,
      messType: "text",
      seen: false,
      recorder: false,
    };
  },
  computed: {
    role() {
      return this.$store.getters.role;
    },
    userId() {
      return this.$store.getters.userId;
    },
    // filteredUsers() {
    //   return [...this.users].sort((a, b) => +a.mssg < +b.msg);
    // },
  },
  watch: {
    message(val) {
      if (/\.(jpg|jpeg|png|webp|avif|gif|svg)$/.test(val)) {
        this.imgBlobUrl = this.message;
        this.messType = "image";
      } else {
        this.messType = "text";
      }
    },
    users(val) {
      this.filteredUsers = [...val].sort((a, b) => +b.msgs - +a.msgs);
    },
  },
  methods: {
    sendMessage() {
      const message = new Message({
        message: this.message,
        type: this.messType,
      });

      if (!this.choosenUser) return;
      if (this.role == 1) message.setMessage(this.choosenUser);
      if (!message.validate()) return alert("بعض المعلومات ناقصة");
      if (this.image) return this.uploadImage(message);
      this.addMessage(message);
      if (this.role == 0) this.socket.emit("sendMessage", message);
      else if (this.role == 1) this.socket.emit("sendMessageAdmin", message);
    },
    chooseChat(user) {
      if (this.role == 0) return;
      this.messages = [];
      this.choosenUser = user;
      this.openList = false;
      this.messagesLoading = true;
      this.seen = false;
      Axios.get(`/getMessages/${user._id}/${0}/${this.limit}`).then((res) => {
        this.messagesLoading = false;
        if (res.data.err) return;
        const msgs = res.data.messages.sort(
          (a, b) => new Date(a.date).getTime() - new Date(b.date).getTime()
        );
        this.messages = msgs;
        this.socket.emit("lastSeenAdmin", user._id);
        const index = this.users.findIndex((u) => u._id == user._id);
        this.users[index]["msgs"] = 0;
        this.isSeen("admin");
        setTimeout(() => this.scrollDown(), 500);
      });
    },
    getUnreadMsgs() {
      for (let user of this.users) {
        const index = this.users.findIndex((u) => u._id == user._id);
        if (new Date(user.lastSeenAdmin).getTime() < new Date(user.sent).getTime()) {
          this.users[index]["msgs"] = 1;
        } else this.users[index]["msgs"] = 0;
      }
      this.users = [...this.users];
    },
    search(event) {
      this.filteredUsers = this.users.filter((user) => {
        return (
          user.fullname.toLowerCase().includes(event.target.value) ||
          user.phone.includes(event.target.value) ||
          // user.parentPhone.includes(event.target.value) ||
          user.address.toLowerCase().includes(event.target.value) ||
          user.email.toLowerCase().includes(event.target.value)
        );
      });
    },
    deleteMessage(code, mess) {
      if (!confirm("مسح الرسالة للكل")) return;
      const data = { code, mess: "" };
      if (mess.includes("imageId_")) data["mess"] = mess;
      this.socket.emit("deleteMsg", data);
    },
    loadMore() {
      this.messagesLoading = true;
      Axios.get(
        "/getMessages/" +
          this.choosenUser._id +
          "/" +
          this.messages.length +
          "/" +
          this.limit
      ).then((res) => {
        this.messagesLoading = false;
        const newMsgs = res.data.messages.sort(
          (a, b) => new Date(a.date).getTime() - new Date(b.date).getTime()
        );
        this.messages = [...newMsgs, ...this.messages];
      });
    },
    pickImage(e) {
      const file = e.target.files[0];
      if (file.size > 1024 * 1024 * 4) return alert("اختار صورة أصغر");
      this.image = file;
      const blob = new Blob([file]);
      this.imgBlobUrl = window.URL.createObjectURL(blob);
    },
    scrollDown() {
      const messages = document.getElementById("messages");
      messages.scrollTop = messages.scrollHeight + 700;
    },
    removeToSendImg() {
      window.URL.revokeObjectURL(this.imgBlobUrl);
      this.imgBlobUrl = null;
      this.image = null;
      this.message = "";
      this.uploading = false;
    },
    uploadImage(message) {
      this.uploading = true;
      message.setProp("type", "image");
      const formData = message.setFormData();
      formData.append("file", this.image, "imageId_" + new Date().getTime().toString());
      Axios.post("/uploadChatFile", formData).then((res) => {
        this.uploading = false;
        if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
        this.removeToSendImg();
        this.addMessage(res.data.message);
        if (this.role == 0) this.socket.emit("sendMessage", res.data.message);
        else if (this.role == 1) this.socket.emit("sendMessageAdmin", res.data.message);
      });
    },
    reloadImg(code, src) {
      const index = this.messages.findIndex((mess) => mess.code == code);
      this.messages[index].src = src;
    },
    addMessage(mess) {
      const index = this.messages.findIndex((message) => message.code == mess.code);
      if (index > -1) return;
      this.messages = [...this.messages, mess];
      if (mess.senderId == this.userId) {
        this.message = "";
        this.imgBlobUrl = null;
        this.scrollDown();
      }
    },
    pickEmoji(emoji) {
      this.message = this.message + emoji;
    },
    isSeen(peer) {
      Axios.get(`/isSeen/${this.choosenUser._id}/${peer}`).then((res) => {
        this.seen = res.data.seen;
      });
    },
    secureUrl(src) {
      if (src.includes("https://")) return src;
      else if (src.includes("http://")) {
        return src.replace("http://", "https://");
      }
    },
  },
};
</script>

