<template>
  <div>
    <div class="audio" :class="direction">
      <audio preload="auto" ref="audio" controls>
        <source :src="src" type="audio/mp3" />
      </audio>
      <div @click="changeSpeed" class="speed">{{ speeds[index] }}x</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["src", "direction"],
  mounted() {
    const audio = this.$refs.audio;
    audio.ontimeupdate = (e) => console.log(e.target.currentTime);
    console.log(audio.duration);
    audio.onloadedmetadata = (e) => console.log(audio.duration);
    // audio.addeventlistner("play", (e) => console.log(e));
  },
  data() {
    return {
      index: 2,
      speeds: [0.5, 0.75, 1, 1.25, 1.5, 1.75, 2],
    };
  },
  methods: {
    changeSpeed() {
      this.index = this.index == this.speeds.length - 1 ? 0 : this.index + 1;
      this.$refs.audio.playbackRate = this.speeds[this.index];
    },
  },
};
</script>

