<template>
  <div>
    <div v-if="mediaRecorder" class="recorder">
      <div class="timer">
        <h4 class="text-center">{{ timer }}</h4>
        <div v-if="uploading" class="center">
          <loading />
        </div>
      </div>
      <div class="ctrls">
        <div class="ctrl">
          <div v-if="audioChunks.length < limit">
            <i v-if="state == 'inactive'" @click="startRecord" class="fa fa-circle"></i>
            <i v-if="state == 'paused'" @click="pauseResumeRecord" class="fa fa-play"></i>
            <i
              v-if="state == 'recording'"
              @click="pauseResumeRecord"
              class="fa fa-pause"
            ></i>
          </div>

          <div v-if="audioChunks.length >= limit">
            <i class="fa fa-ban"></i>
          </div>
        </div>
        <div @click="deleteRecord" class="ctrl">
          <i class="fa fa-trash"></i>
        </div>
        <div @click="finishRecord" class="ctrl">
          <i class="fa fa-send"></i>
        </div>
      </div>
    </div>
    <!---------------------->
    <div v-if="!mediaRecorder" class="center">
      <loading />
    </div>
  </div>
</template>

<script>
import { Message } from "./message.js";
import { socket } from "../../main.js";
import Axios from "axios";
export default {
  props: ["choosenUser"],
  mounted() {
    this.initiate();
  },
  destroyed() {
    this.mediaDevices = null;
  },
  data() {
    return {
      mediaRecorder: null,
      recordedVoice: null,
      audioChunks: [],
      limit: 180,
      state: "inactive",
      socket: socket(),
      uploading: false,
    };
  },
  computed: {
    duration() {
      if (this.audioChunks.length > this.limit) {
        this.stopRecord();
      }
      return this.audioChunks.length;
    },
    timer() {
      return new Date(this.duration * 1000).toString().substring(19, 24);
    },
    role() {
      return this.$store.getters.role;
    },
  },
  methods: {
    initiate() {
      // navigator.getUserMedia =
      //   window.navigator.mediaDevices.getUserMedia ||
      //   navigator.mediaDevices.webkitGetUserMedia ||
      //   navigator.mediaDevices.mozGetUserMedia;
      if (!navigator.mediaDevices?.getUserMedia)
        return alert("جهازك لا يدعم استخدام الميديا");
      navigator.mediaDevices
        .getUserMedia({ audio: true })
        .then((stream) => {
          this.mediaRecorder = new MediaRecorder(stream);
        })
        .catch((err) => alert(err));
      // navigator.mediaDevices.getUserMedia(
      //   { audio: true },
      //   (stream) => (this.mediaRecorder = new MediaRecorder(stream)),
      //   (err) => alert(err)
      // );
    },
    startRecord() {
      if (this.mediaRecorder.state != "inactive") return;
      this.mediaRecorder.start(1000);
      this.state = this.mediaRecorder.state;
      this.mediaRecorder.ondataavailable = (event) => {
        this.audioChunks.push(event.data);
      };
    },

    pauseResumeRecord() {
      if (this.mediaRecorder.state == "inactive") return;
      if (this.mediaRecorder.state == "recording") {
        this.mediaRecorder.pause();
      } else {
        this.mediaRecorder.resume();
      }
      this.state = this.mediaRecorder.state;
    },
    stopRecord() {
      if (this.mediaRecorder.state == "inactive") return;
      this.mediaRecorder.stop();
      this.state = this.mediaRecorder.state;
    },
    finishRecord() {
      this.stopRecord();
      // this.mediaRecorder.addEventListener("stop", () => {
      if (!this.uploading) this.sendRecord();
      // });
    },
    deleteRecord() {
      this.stopRecord();
      setTimeout(() => {
        this.audioChunks = [];
        this.state = this.mediaRecorder.state;
      }, 1000);
    },
    async sendRecord() {
      const message = new Message({ type: "audio" });
      if (this.role == 1) message.setMessage(this.choosenUser);
      const formData = message.setFormData();
      const file = new File(this.audioChunks, "record", { type: "audio/mp3" });
      formData.append("file", file, "imageId_" + new Date().getTime().toString());
      this.uploading = true;
      Axios.post("/uploadChatFile", formData).then(async (res) => {
        this.uploading = false;
        if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
        this.audioChunks = [];
        if (this.role == 0) this.socket.emit("sendMessage", res.data.message);
        else if (this.role == 1) this.socket.emit("sendMessageAdmin", res.data.message);
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.recorder {
  .ctrls {
    width: 80%;
    margin: 5px auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid #111;
    padding: 5px;
    border-radius: 5px;
  }
}
</style>
