import store from "../../store";

export class Message {
    constructor({
        message = "",
        senderId = store.getters.userId,
        receiverId="admin",
        senderName = store.getters.username,
        phone = "",
        stage = store.getters.stage,
        role = store.getters.role,
        room = store.getters.userId,
        type = "text"
    }) {
        this.message = message;
        this.senderId = senderId;
        this.receiverId = receiverId;
        this.senderName = senderName;
        this.phone = phone;
        this.stage = stage;
        this.role = role;
        this.room = room;
        this.type = type;
        this.date = new Date();
        this.code = Math.random().toString(16).substring(3, 15);
    }
    setProp(prop, val) {
        this[prop] = val;
    }
    setMessage(user) {
        if(store.getters.role == 0) return; 
        if(user._id) this.receiverId = user._id;
        if(user.phone) this.phone = user.phone;
        if(user.stage) this.stage = user.stage;
        if(user._id) this.room = user._id;
    }
    validate() {
        if(this.receiverId == '' || this.username == '' || this.stage == '' || this.date == '') return false;
        return true;
    }
    setFormData() {
        const formData = new FormData();
        formData.append("senderId", this.senderId);
        formData.append("receiverId", this.receiverId);
        formData.append("senderName", this.senderName);
        formData.append("phone", this.phone);
        formData.append("stage", this.stage);
        formData.append("role", this.role);
        formData.append("room", this.room);
        formData.append("type", this.type);
        formData.append("date", this.date);
        formData.append("code", this.code);
        return formData;
    }
}
